import React from 'react'
import {Layout} from 'antd'
import style from './styles.module.less'
import SiderMenu from './component/sidermenu/index.js'
import HeaderMenu from './component/headermenu/index.js'

class Layouter extends React.Component{
  render() {
    return (
      <div id={style.wrap}>
        <Layout className={style.layoutContain}>
          <SiderMenu/>
          <Layout>
            <HeaderMenu/>
            {this.props.children}
          </Layout>
        </Layout>
      </div>
    )
  }
}

export default Layouter



