import React from 'react'
import {Icon} from 'antd'
import style from './styles.module.less'
import HeaderSerch from '../headersearch/index.js'

class HeaderCommon extends React.Component{
  render(){
    return (
      <div className={style.headerSerch}>
        <HeaderSerch></HeaderSerch>
        <span>
          <Icon type="form"/>
        </span>
        <span>
          <Icon type="user"/>
        </span>
      </div>
    )
  }
}

export default HeaderCommon



