import React from 'react'
import {
    HashRouter as Router,
    Route,
    Redirect,
    Switch,
} from 'react-router-dom';
import Layouter from 'component/layout/index.js'

export default function RenderRouter() {
    const routesReq = require.context('container', true, /index\.jsx$/) || {};
    const cusRoutes = require.context('container', true, /route\.js$/) || {};
    const routes = [];
    const routeReg = /\/.+\//g;
    const keySet = new Set();


    if (cusRoutes.keys().length > 0) {
        cusRoutes.keys().forEach(key => {
            keySet.add(...key.match(routeReg));
            routes.push(cusRoutes(key).default)
        })
    }

    routesReq.keys().forEach(key => {

        const r = key.match(routeReg);

        if(keySet.has(...r)) return;

        const matches = key.match(/^.(\/[\w\-]+){1,3}\/index.jsx$/);
        if (matches) {

            let path = key.split('.')[1];
            path = path.split('/index')[0];

            let name = path;
            let component = routesReq(key).default;


            routes.push({
                path: path,
                name: name,
                component: component
            });
        }
    });


    const RouteWithSubRoutes = (route) => (
        <Route path={route.path} render={props => (
            <route.component {...props} routes={route.routes}/>
        )}/>
    )

    return (
        <Router>
            <Layouter>
                <Switch>
                    {routes.map((route, i) => (
                        <RouteWithSubRoutes key={i} {...route} />
                    ))}
                    <Redirect to='/Home'/>
                </Switch>
            </Layouter>
        </Router>
    )


}



