import React, {Component} from 'react';
import {Button} from 'antd';
import {connect} from 'react-redux'
import changeSubnet from './actions';
import test from 'assets/test.jpg';
import {Link, withRouter} from 'react-router-dom';
import pureRender from 'util/pureRender';
import styles from './styles.module.less';

/**
 * 加上这个装饰器子组件可以获取路由history属性
 */
@withRouter
class GetRouterHistory extends Component {


    componentDidMount() {

    }

    sonson() {

    }

    render() {

        return (
            <div>测试子组件获取路由history属性</div>
        )
    }
}


@connect(state => ({
    page: state.page
}), {changeSubnet})

@pureRender

class Page extends Component {
    testReducer() {
        this.props.changeSubnet()
    }

    render() {
        return (
            <div className="App">
                <GetRouterHistory parent={this}/>
                <div className={styles.container}>我是测试less</div>
                <ul>
                    <li><Link to="/Home">Home（测试路由）</Link></li>
                    <li><Link to="/App">App（测试路由）</Link></li>
                    {/*<li><Link to="/test:params">App</Link></li>*/}
                    <li><Link to="/404">我啥也不是，你点我干啥（测试路由）</Link></li>
                </ul>

                <div>测试图片</div>
                <img src={test} alt="test"/>
                <Button type="primary" onClick={() => {
                    this.testReducer()
                }} disabled={this.props.page.subnetButton}>Subnet</Button>
            </div>
        );
    }
}


export default Page;






