import React, { Component } from 'react';
import { Button } from 'antd';

class Home extends Component {

    constructor(props){
        super(props);
        this.state = {
            name:''
        }
    }



    componentDidMount() {

    }

    render() {
        return (
            <div className="App">
                <div>项目名字：{this.state.name}</div>
                <Button type="primary">我是home</Button>
            </div>
        );
    }
}

export default Home;


