import  React from 'react'
import classNames from  'classnames'
import style from './styles.module.less'
/***********************************************************/
import MainMenu from '../mainmenu/index.js'
// import MainMenu from '../mainmenu/index.js'
import ManagerSideMenu from '../managesidemenu/index.js'
/***********************************************************/
class SiderMenu extends React.Component{
  state={
    isOpened: false
  }
  goManageMenu=()=>{
    this.setState({isOpened: true})
  }
  backMainmenu=()=>{
    this.setState({isOpened: false})
  }
  render(){
    const managemenuWrap = classNames(style.managemenuWrap, {
      [style.showmenu]: this.state.isOpened})
    return (
      <div className={style.sidemenuWrap}>
        <div className={style.mainmenuWrap} >
          <MainMenu gomanagemenu={this.goManageMenu}/>
        </div>
        <div className={managemenuWrap} >
          <ManagerSideMenu backmainmenu={this.backMainmenu}/>
        </div>
      </div>
    )
  }
}

export default SiderMenu


