import React from 'react'
import {Link} from 'react-router-dom'
import {Layout, Menu, Icon} from 'antd'
import style from './styles.module.less'
import mainMenuData from '../../../../assets/data/sidemainmenu.json'
const {Sider} = Layout
const SubMenu = Menu.SubMenu


class MainMenu extends React.Component{
  rootSubmenuKeys = ['2','3']
  state={
    collapsed: false,
    openKeys: []
  }


  //侧边栏的关闭展开
  toggle=()=>{
    this.setState({
      collapsed: !this.state.collapsed
    })
  }
  //点击管理弹出管理侧边栏 以及点击其他条目,关闭现在展开的submenu
  handleClick=(e)=>{
    let key =e.key
    let keyGroup = key.split('')[0]
    if(this.state.openKeys[0]!==keyGroup){
      this.setState({
        openKeys: []
      })
    }
    //如果点击了管理 显示管理侧边栏
    if(key === '8'){
      this.props.gomanagemenu()
    }
  }
  onOpenChange=(openKeys)=>{
    const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
    if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      this.setState({ openKeys });
    } else {
      this.setState({
        openKeys: latestOpenKey ? [latestOpenKey] : [],
      });
    }
  }
  //渲染menuItem的方法
  menuItem=(dataItem)=>{
    let menuitem = dataItem.key!=='8' ? //管理的那一项(key='8')没有Link
                        <Menu.Item key={dataItem.key} className={style.menuItem}>
                          <Link to={dataItem.url}>
                            <Icon type={dataItem.icon} />
                            <span>{dataItem.name}</span>
                          </Link>
                        </Menu.Item>
                     : <Menu.Item key={dataItem.key} className={style.menuItem}>
                            <Icon type={dataItem.icon} />
                            <span>{dataItem.name}</span>
                        </Menu.Item>  
    return menuitem
  }
  //渲染submenu的方法
  subMenu=(dataItem)=>{
    let subMenuitem = dataItem.item
    let submenu = <SubMenu key={dataItem.key} title={<span className={style.submenutitle}><Icon type={dataItem.title.icon} /><span>{dataItem.title.name}</span></span>} className={style.submenu}>
                         {subMenuitem.map((v,i)=>{
                            let temp = <Menu.Item key={v.key} className={style.subMenuItem}>
                                            <Link to={v.url}>{v.name}</Link>
                                         </Menu.Item>
                            return temp
                         })}
                    </SubMenu>
    return submenu
  }

  
  render(){
    const acmp = <div className={style.cmp}>ACMP</div>
    const data = mainMenuData.list

    return (      
      <Sider
        className={style.mainsider}
        trigger={null}
        collapsible
        collapsed={this.state.collapsed}
      >
        {/*默认侧边栏收起 , 如果要修改成默认侧边栏打开,则collapsed属性去掉 !    设置起属性默认是true    ?style.logoShrin:style.log, ?null:cmp*/}
        {/*默认侧边栏展开 , 如果要修改成默认侧边栏收起,则collapsed属性要取非 !  设置起属性默认是false    ?style.log:style.logoShrink, ?null:cmp*/}
        <div className={this.state.collapsed ? style.logo : style.logoShrink }>
          {this.state.collapsed ? null : acmp}
            <Icon
              style={{cursor: 'pointer'}}/*只在行内样式里生效*/
              className={style.trigger}
              type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
              onClick={this.toggle}
            />
            </div>
            <Menu theme="light" mode="inline" defaultSelectedKeys={['1']} className={style.menu} onClick={this.handleClick} openKeys={this.state.openKeys} onOpenChange={this.onOpenChange}>
              {data.map((v,i)=>{
                let item = v.type==='menuitem'? this.menuItem(v) : this.subMenu(v)
                return item
              })}
            </Menu>
          </Sider>
    )
  }
}

export default MainMenu


