import  React from 'react'
import {Link} from 'react-router-dom'
import {Layout, Menu, Icon} from 'antd'
import style from './styles.module.less'
import manageMenuData from '../../../../assets/data/sidemanagemenu.json'
const {Sider} = Layout
const MenuItem = Menu.Item
const MenuItemGroup = Menu.ItemGroup


class ManagerSideMenu extends React.Component{
  rootSubmenuKeys = ['2','3']
  state={
    collapsed: false,
    openKeys: []
  }

  toggle=()=>{
    this.setState({
      collapsed: !this.state.collapsed
    })
  }
  handleClick=(e)=>{
    this.setState({
      openKeys: []
    })
  }
  onOpenChange = (openKeys) => {
    const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
    if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      this.setState({ openKeys });
    } else {
      this.setState({
        openKeys: latestOpenKey ? [latestOpenKey] : [],
      });
    }
  }
  //根据json数据渲染menuItem
  menuItem=(v)=>{
    let menuitem = <Menu.Item key={v.key} className={style.menuItem}>
                      <Link to={v.url}>
                        <Icon type={v.icon}/>
                        <span>{v.name}</span>
                      </Link>
                   </Menu.Item>

    return menuitem
  }
  //根据json数据渲染menuItemGroup
  menuItemGroup=(v)=>{
    let item = v.item
    let menuitemGroup = <MenuItemGroup key={v.key} title={v.title} className={style.menugrouptitle}>
                          {item.map((u,i)=>{
                              let temp =  <MenuItem key={u.key} >
                                            <Link to={u.url}>
                                              {u.name}
                                            </Link>
                                          </MenuItem>

                              return temp
                            })}
                        </MenuItemGroup>

    return menuitemGroup
  }


  render(){
    const backButton= <div>{"< 返回主菜单"}</div>
    const data = manageMenuData.list

    return (
      <Sider
        className={style.managesider}
        trigger={null}
        collapsible
      >
        {/*点击返回主菜单*/}
            <div className={style.back} onClick={this.props.backmainmenu}>
              {backButton}
            </div>
            <Menu theme="light" mode="inline" defaultSelectedKeys={['1']} className={style.menu} onClick={this.handleClick} openKeys={this.state.openKeys} onOpenChange={this.onOpenChange}>
                {data.map((v,i)=>{
                  let item = v.type==='menuitem' ? this.menuItem(v) : this.menuItemGroup(v)
                  return item
                })}
            </Menu>
          </Sider>
    )
  }
}

export default ManagerSideMenu


